<template>

    <main class="profile-content row" v-if="profile">
        <header class="profile-header col-md-12">
            <div class="row">
                <div class="contact-info col-md-4" v-if="profile.contact">
                    <figure class="avatar" v-if="profile.contact.avatar">
                        <img :src="profile.contact.avatar" alt="user avatar" >
                    </figure>
                    
                    <address class="contact" v-if="profile.contact.address">
                        <ul>
                            <li v-for="(info, i) in profile.contact.address" :key="i">
                                <a :href="info.link" target="_blank"><i :class="info.icon"></i>&nbsp;{{ info.desc }}</a>
                            </li>
                            <li v-for="(social,i) in profile.socials" :key="i">
                                <a :href="social.link" target="_blank"><i :class="social.icon"></i>&nbsp;{{ social.desc }}</a>
                            </li>
                        </ul>
                    </address>
                </div>
                <section class="title col-md-8" v-if="profile.contact">
                    <div class="user-name" v-if="profile.contact.name">{{ profile.contact.name }}</div>
                    <div class="job-title" v-if="profile.jobTitle">{{ profile.jobTitle }}</div>
                </section>
            </div>
        </header>
        
        <article class="print col-md-12 active" v-if="profile">
            <div class="right">
            
                <section class="education" v-if="profile.educations">
                    <div class="title-wrap">
                        <span>{{ $t('message.educations') }}</span>
                    </div>
                    <div class="content" v-for="(education,i) in profile.educations" :class="i" :key="i">
                        <span v-for="(value,ii) in education" :class="ii" :key="ii">
                            {{value}}
                        </span>
                    </div>
                </section>
                
                <section class="works">
                    <div class="title-wrap">
                        <span>{{ $t('message.works') }}</span>
                    </div>
                    <div class="content">
                        <time-line :items="profile.works" :page="1" :print="true"></time-line>
                    </div>
                </section>
            </div>
            <div class="right">
                <section class="projects">
                    <div class="title-wrap">
                        <span>{{ $t('message.projects') }}</span>
                    </div>
                    <div class="content">
                        <time-line :items="profile.projects" :page="2" :print="true"></time-line>
                    </div>
                </section>
                <section class="skill-desc">
                    <div class="title-wrap">
                        <span>{{ $t('message.skills') }}</span>
                    </div>
                    <div class="content row">
                        <ul class="skill-desc col-md-12">
                            <li v-for="(skill,i) in profile.skillsDesc" :key="i">
                                {{skill}}
                            </li>
                        </ul>
                    </div>
                </section>
            </div>
        </article>
    </main>

</template>

<script>
    //import { mapActions, mapGetters, mapState } from 'vuex';

    import Left from '../common/left.vue';
    import TimeLine from '../common/timeline.vue';

    export default {

        props:{
            profile: {
                type:Object,
                default:null
            }
        },

        data() {
            return {
                
            }
        },

        components: {
            Left,
            TimeLine
        },

        methods: {
            
        }
    };
</script>

<style scoped lang="less">
 .profile-header{
        padding: 30px 15px;
        background-color: #00BFA5;
        border-radius: 4px;
        box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.5);
        .avatar{
            img{
                max-width: 160px;
                max-height: 160px;
                border-radius: 50%;
                vertical-align: middle;
            }
        }
        li{
            font-style: normal;
            font-size: 18px;
            word-break: break-all;
            a{
                color: rgba(255,255,255,0.8);
                transition: all .3s ease-out;
                padding-left: 5px;
                vertical-align: middle;
                &:hover{
                    color: rgba(255,255,255,1);
                }
            }
            &:before{
                font-family: FontAwesome;
                color: rgba(255,255,255,0.9);
            }
            &.tel:before{
                content: "\f095";
            }
            &.email:before{
                content: "\f0e0";
            }
            &.github:before{
                content: "\f092";
            }
            &.blog:before{
                content: "\f09e";
            }
            &.weibo:before{
                content: "\f18a";
            }

            &#tel{
                display:none
            }
        }

        .contact{
            ul{
                list-style: none;
                padding: 0;
            }
        }
        .title{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .user-name{
                font-size: 40px;
                color: rgba(255,255,255,0.8);
                margin: 5px 0;
            }
            .job-title{
                font-size: 36px;
                color: rgba(255,255,255,0.8);
                margin: 5px 0;
            }
        }
    }
    
    .profile-content{
        article{
            background-color: #f0f2f1;
            border-radius: 4px;
            box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.5);
            &.active{
                transform: rotateY(0deg);
            }

            &.inactive{
                transform: rotateY(180deg);
                display: none;
            }

            .right{
                padding: 15px 20px;
                border-top-right-radius: 4px;
                border-bottom-right-radius: 4px;

                section{
                    margin-bottom: 25px;
                    .content{
                        display: flex;
                        margin: 15px 0 10px 33px;
                        font-size: 17px;
                    }
                    .title-wrap{
                        font-size: 22px;
                        color: #00796b;
                        line-height: 30px;
                        border-bottom: 1px solid #00796b;
                        span{
                            padding-left: 7px;
                        }
                    }
                    &.education{
                        .title-wrap:before{
                            font-family: FontAwesome;
                            content: '\f02d';
                        }
                    }
                    &.works{
                        .title-wrap:before{
                            font-family: FontAwesome;
                            content: '\f0b1';
                        }
                    }
                    &.projects{
                        .title-wrap:before{
                            font-family: FontAwesome;
                            content: '\f0f6';
                        }
                    }
                    &.skill-desc ul.skill-desc{
                        .title-wrap:before{
                            font-family: FontAwesome;
                            content: '\f0ad';
                        }
                    }
                }

                .skill-desc{
                    
                    ul.skill-desc{
                        list-style: none;
                        li{
                            padding-bottom: 5px;
                            font-size: 15px;
                            line-height: 1.5;
                            &:last-child{
                                padding-bottom: 0;
                            }
                            &:before{
                                font-family: FontAwesome;
                                content: "\f005";
                                //padding-right: 7px;
                                font-size: 14px;
                            }
                        }
                    }
                    
                }
            }
        }
    }

    .skills{
            list-style: none;
            li{
                position: relative;
                margin-bottom: 10px;
            }
            .skill-name {
                position: relative;
                top: -3px;
                width: 100%;
                font-size: 14px;
                display: inline-block;
                word-break: normal;
            }
            .skill-bar-wrap {
                padding: 0;
                margin: 0;
                height: 17px;
                background: #00796b;
                display: inline-block;
                border-radius: 20px;
                .skill-bar {
                    height: 17px;
                    background: #fff;
                    border-radius: 20px;
                    margin-left: -1px;
                }
            }
        }
</style>



