<template>
    <div class="left">
        <div class="wisdom" v-show="data.wisdom">{{data.wisdom}}</div>
        <div class="info">
            <div class="tag">{{ $t('message.socials') }}</div>
            <ul class="social-contact-list">
                <li v-for="(social,i) in data.socials" :key="i">
                    <a :href="social.link" target="_blank"><i :class="social.icon"></i>&nbsp;{{ social.desc }}</a>
                </li>
            </ul>
        </div>
        <div class="info">
            <div class="tag">{{ $t('message.jobs') }}</div>
            <ul class="job-intent">
                <li v-for="(value,ii) in data.jobs" :key="ii">
                    {{ii}}: {{value}}
                </li>
            </ul>
        </div>
        <div class="info">
            <div class="tag">{{ $t('message.skills') }}</div>
            <ul class="skills">
                <li v-for="(value,iii) in data.skills" :key="iii">
                    <span class="skill-name">{{iii}}</span>
                    <div class="skill-bar-wrap">
                        <div class="skill-bar" :style="{width:value +'%'}"></div>
                    </div>
                </li>
            </ul>
        </div>
        <div class="info">
            <div class="tag">{{ $t('message.evaluation') }}</div>
            <p class="self-evaluation">
                {{data.evaluation}}
            </p>
        </div>
    </div>
</template>

<style scoped lang="less">
    .left{
        padding: 15px 15px;
        border-top-left-radius: 4px;
        border-bottom-left-radius: 4px;
        background-color: #00BFA5;
        .wisdom{
            margin: 25px 0 20px;
            font-size:16px;
            line-height: 1.5;
            color: #FFFFFF;
            text-align: center;
        }
        .info{
            margin-bottom: 25px;
            .tag{
                position: relative;
                font-size: 18px;
                color: #9f3;
                &:before {
                    padding-right: 5px;
                }
            }
            ul{
                list-style: none;
                padding: 0;
            }
            li{
                color: #FFF;
                padding: 10px 0 0 20px;
                cursor: default;
                a{
                    color: #FFF;
                }
            }

            .self-evaluation{
                color: #fff;
                text-indent: 18px;
                margin-top: 15px;
                line-height: 1.5;
                word-wrap: break-word;
                overflow : hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 7;
                -webkit-box-orient: vertical;
            }
        }

        .social-contact-list{
            li{
                &:first-child{
                    &:before{
                        
                    }
                }
                &:last-child{
                    &:before{
                        
                        padding-right: 2px;
                    }
                }
            }
        }

        .job-intent{
            li{
                &:nth-child(1){
                    &:before{
                        
                    }
                }
                &:nth-child(2){
                    &:before{
                        
                        
                    }
                }
                &:nth-child(3){
                    &:before{
                        
                    }
                }
            }
        }

        .skills{
            li{
                position: relative;
            }
            .skill-name {
                position: relative;
                top: -3px;
                width: 100%;
                font-size: 14px;
                display: inline-block;
                word-break: normal;
            }
            .skill-bar-wrap {
                height: 17px;
                width: 100%;
                background: #00796b;
                display: inline-block;
                border-radius: 20px;
                .skill-bar {
                    height: 17px;
                    background: #fff;
                    border-radius: 20px 0 0 20px;
                }
            }
        }
    }

    @media screen and (max-width: 768px){
        .left{
            display: none;
        }
    }

</style>
<script>

    export default {
        props:{
            data:{
                type:Object,
                required:true,
                default: function() {
                    return {}
                }
            }
        }
    }

</script>



