<template>
    <div class="container">
        <div class="row justify-content-center align-items-center">
            <div class="vc--resume col-md-12" ref="resume">
                <loading v-if="fetching"></loading>
                <resume-content v-if="!isPrint && !fetching" :profile="profile"></resume-content>
                <print-content v-if="isPrint && !fetching" :profile="profile"></print-content>
            </div>
            <aside class="float-right" v-if="!fetching && !isPrint">
                <ul>
                    <li>
                        <a href="#" @click.stop="print"><i class="fa fa-print"></i>&nbsp;Print</a>
                    </li>
                    <li>
                        <a href="#" @click.stop="png"><i class="fa fa-image"></i>&nbsp;PNG</a>
                    </li>
                </ul>
            </aside>
        </div>
    </div>
</template>

<script>
    import { mapGetters, mapActions } from 'vuex';

    import Loading from './components/Loading.vue';
    import ResumeContent from './components/ResumeContent.vue';
    import PrintContent from './components/PrintContent.vue';
    
    import html2canvas from 'html2canvas';
    import FileSaver from 'file-saver';

    export default {

        data() {
            return {
                msg:"APP",
                resume:null,
            }
        },

        computed:{
            ...mapGetters({
               fetching: 'getFetching',
               isPrint: 'getPrint',
               profile: 'getProfile'
            }),
        },

        methods:{
            ...mapActions(['getResume','setPrint']),
            print() {
                let me = this;
                me.setPrint(true);
                
                var onPrintFinished= function(printed) {
                    me.setPrint(false);
                }
                
                setTimeout( function(){
                    onPrintFinished(window.print());
                }, 1000);
                
            },
            png() {
                this.resume = this.$refs.resume;
                this.setPrint(true);
                let me = this;
                setTimeout( function() {
                    html2canvas(me.resume).then(function (canvas) {
                        canvas.toBlob(function (blob) {
                            FileSaver.saveAs(blob, 'cv.png');
                            me.setPrint(false);
                        })
                    });
                }, 1000);
            }
        },

        created() {
            this.getResume();
        },

        components:{
            ResumeContent,
            PrintContent,
            Loading
        }
    };
</script>

<style scoped lang="less">
    ul{
        list-style: none;
    }
    @media screen and (min-width:960px){
        .vc--resume {
            max-width: 960px;
            margin: 15px auto;
        }
    }

    @media print {
        body {
            background: transparent;
        }

        .vc--resume{
            background: transparent;
            width: 1000px;
            margin-top: 0;
        }
    }
    @media print {
        .float-right {
            display: none;
        }
    }

    @media screen and (min-width: 1140px){
        .float-right{
            position: fixed;
            right: 0;
            top: 30%;
            z-index: 2;
            li{
                margin-bottom: 10px;
                border-radius: 10px 0 0 10px;
                box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);
                background-color: #00BFA5;
                cursor: pointer;
                a{
                    display: inline-block;
                    width: 80px;
                    height: 38px;
                    line-height: 38px;
                    padding-left: 10px;
                    color: rgba(255,255,255,0.8);
                    transition: all .3s ease-out;
                    &:hover{
                        color: rgba(255,255,255,1);
                    }
                }
            }
        }
    }

    @media screen and (max-width: 1140px){
        .float-right{
            display: none;
        }
    }
</style>




