<template>
    <main class="profile-content row" v-if="profile">
        <header class="profile-header col-md-12" >
            <div class="row">
                <div class="contact-info col-md-4" v-if="profile.contact">
                    <figure class="avatar" v-if="profile.contact.avatar">
                        <img :src="profile.contact.avatar" alt="user avatar" >
                    </figure>
                    
                    <address class="contact" v-if="profile.contact.address">
                        <ul>
                            <li v-for="(info, i) in profile.contact.address" :key="i">
                                <a :href="info.link" target="_blank"><i :class="info.icon"></i>&nbsp;{{ info.desc }}</a>
                            </li>
                        </ul>
                    </address>
                </div>
                <section class="title col-md-8" v-if="profile.contact">
                    <div class="user-name" v-if="profile.contact.name">{{ profile.contact.name }}</div>
                    <div class="job-title" v-if="profile.jobTitle">{{ profile.jobTitle }}</div>
                </section>
            </div>
        </header>
        <article class="col-md-12 active" @click="page==2" v-if="profile && page==1">
            <div class="row">
                <Left class="col-md-3" :data="profile"></Left>
                <div class="right col-md-9">
                    <section class="education" v-if="profile.educations">
                        <div class="title-wrap">
                            <span>{{ $t('message.educations') }}</span>
                        </div>
                        <div class="content" v-for="(education,i) in profile.educations" :class="i" :key="i">
                            <span v-for="(value,ii) in education" :class="ii" :key="ii">
                                {{value}}
                            </span>
                        </div>
                    </section>
                    <section class="works" v-if="profile.works">
                        <div class="title-wrap">
                            <span>{{ $t('message.works') }}</span>
                        </div>
                        <div class="content">
                            <time-line :items="profile.works" :page="1"></time-line>
                        </div>
                    </section>
                </div>
            </div>
        </article>
        <article class="col-12 active" @click="page==1" v-if="profile && page==2">
            <div class="row">
                <Left class="col-md-3" :data="profile"></Left>
                <div class="right col-md-9">
                    <section class="projects" v-if="profile.projects">
                        <div class="title-wrap">
                            <span>{{ $t('message.projects') }}</span>
                        </div>
                        <div class="content">
                            <time-line :items="profile.projects" :page="2"></time-line>
                        </div>
                    </section>
                    <section class="skill-desc" v-if="profile.skillsDesc">
                        <div class="title-wrap">
                            <span>{{ $t('message.skills') }}</span>
                        </div>
                        <div class="content">
                            <ul class="skill-desc">
                                <li v-for="(skill,ii) in profile.skillsDesc" :key="ii">
                                    {{skill}}
                                </li>
                            </ul>
                        </div>
                    </section>
                </div>
            </div>
        </article>
        
        <footer class="main-footer col-md-12">
            <div class="row">
                <span class="col-4 float-left text-left" @click="setResumePage(1)" v-if="page==2">{{ $t('message.prev') }}</span>
                <div :class="{'offset-4':page==1}" class="col-4 text-center">
                    <a v-if="locale=='vi'" @click="setLang('en')"><img class="flag" :src="flag.en"></a>
                    <a v-if="locale=='en'" @click="setLang('vi')"><img class="flag" :src="flag.vi"></a>
                </div>
                <span class="col-4 float-right text-right" @click="setResumePage(2)"  v-if="page==1">{{ $t('message.next') }}</span>
            </div>
        </footer>
    </main>
</template>

<script>
    import { mapActions, mapState } from 'vuex';

    import Left from '../common/left.vue';
    import TimeLine from '../common/timeline.vue';

    export default {
        props:{
            profile: {
                type:Object,
                default:null
            }
        },
        data() {
            return {
                flag: {
                    vi:'/s/images/vi.svg',
                    en:'/s/images/en.svg'
                }
            }
        },

        components: {
            Left,
            TimeLine
        },

        computed: mapState({
            locale:  state => state.app.locale,
            page:    state => state.cv.page
        }),

        methods: {
            ...mapActions(['setLang','setResumePage'])
        }
    };
</script>

<style scoped lang="less">
 .profile-header{
        padding: 30px 15px;
        background-color: #00BFA5;
        border-radius: 4px;
        box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.5);
        .avatar{
            img{
                max-width: 160px;
                max-height: 160px;
                border-radius: 50%;
                vertical-align: middle;
            }
        }
        li{
            font-style: normal;
            font-size: 18px;
            word-break: break-all;
            a{
                color: rgba(255,255,255,0.8);
                transition: all .3s ease-out;
                padding-left: 5px;
                vertical-align: middle;
                &:hover{
                    color: rgba(255,255,255,1);
                }
            }
            &:before{
                font-family: FontAwesome;
                color: rgba(255,255,255,0.9);
            }
            &.tel:before{
                content: "\f095";
            }
            &.email:before{
                content: "\f0e0";
            }
            &.github:before{
                content: "\f092";
            }
            &.blog:before{
                content: "\f09e";
            }
            &.weibo:before{
                content: "\f18a";
            }

            &#tel{
                display:none
            }
        }

        .contact{
            ul{
                list-style: none;
                padding: 0;
            }
        }
        .title{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            .user-name{
                font-size: 40px;
                color: rgba(255,255,255,0.8);
                margin: 5px 0;
            }
            .job-title{
                font-size: 36px;
                color: rgba(255,255,255,0.8);
                margin: 5px 0;
            }
        }
    }

    @media screen and (max-width: 835px){
        .avatar{
            display: none;
        }
    }

    @media screen and (max-width: 675px){
        .profile-header{
            padding: 0;
        }
    }

    @media screen and (max-width: 520px){
        .profile-header{
            padding: 0;
            .title{
                .user-name{
                    font-size: 36px;
                }
                .job-title{
                    font-size: 18px;
                }
            }
        }
    }

    
    .flag{
        width: 30px;
    }
    @media screen {
        .profile-content {
            .print {
                display: none;
            }
        }
    }
    .profile-content{
        article{
            display: flex;
            align-items: flex-start;
            width: 100%;
            transform-origin: 50% 0%;
            transition: 1s;
            backface-visibility:hidden;
            cursor: pointer;
            background-color: #f0f2f1;
            border-radius: 4px;
            box-shadow: 1px 1px 6px rgba(0, 0, 0, 0.5);
            &.active{
                transform: rotateY(0deg);
            }

            &.inactive{
                transform: rotateY(180deg);
                display: none;
            }

            .right{
                padding: 15px 20px;
                border-top-right-radius: 4px;
                border-bottom-right-radius: 4px;

                section{
                    margin-bottom: 25px;
                    .content{
                        display: flex;
                        margin: 15px 0 10px 33px;
                        font-size: 17px;
                    }
                    .title-wrap{
                        font-size: 22px;
                        color: #00796b;
                        line-height: 30px;
                        border-bottom: 1px solid #00796b;
                        span{
                            padding-left: 7px;
                        }
                    }
                    &.education{
                        .title-wrap:before{
                            font-family: FontAwesome;
                            content: '\f02d';
                        }
                    }
                    &.works{
                        .title-wrap:before{
                            font-family: FontAwesome;
                            content: '\f0b1';
                        }
                    }
                    &.projects{
                        .title-wrap:before{
                            font-family: FontAwesome;
                            content: '\f0f6';
                        }
                    }
                    &.skill-desc{
                        .title-wrap:before{
                            font-family: FontAwesome;
                            content: '\f0ad';
                        }
                    }
                }

                .skill-desc{
                    li{
                        padding-bottom: 5px;
                        font-size: 15px;
                        line-height: 1.5;
                        &:last-child{
                            padding-bottom: 0;
                        }
                        &:before{
                            font-family: FontAwesome;
                            content: "\f005";
                            //padding-right: 7px;
                            font-size: 14px;
                        }
                    }
                }
            }
        }
    }

    .main-footer{
        width: 100%;
        span{
            cursor: pointer;
            display: inline-block;
            padding: 6px 12px;
            border-radius: 4px;
            color: rgba(0,191,165,0.8);
            &:hover{
                color: rgba(0,191,165,1);
            }
        }
        a{
            cursor: pointer;
            display: inline-block;
            padding: 6px 12px;
            border-radius: 4px;
            color: rgba(0,191,165,0.8);
            &:hover{
                color: rgba(0,191,165,1);
            }
        }
    }

    @media screen and (min-width: 768px) and (max-width: 940px){
        .profile-content{
            article{
                .right{
                    section{
                        .content{
                            margin-left: 0;
                        }
                    }
                }
            }
        }
    }

    @media screen and (max-width: 625px){
        .profile-content{
            article{
                .right{
                    section{
                        &.education{
                            .content{
                                span:nth-child(3){
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    @media screen and (max-width: 414px){
        .profile-content{
            article{
                .right{
                    section{
                        &.education{
                            .content{
                                span:last-child{
                                    display: none;
                                }
                            }
                        }
                    }
                }
            }
        }
    }

</style>



